<template lang="html">
  <div class="music-list" ref="musiclist">
    <div class="back" @click="back">
      <icon class="icon-back" name="angle-left" scale="2"></icon>
    </div>
    <div class="collect" @click="toggleCollect" v-show="collectable">
      <icon class="icon-collect" :name="collectIcon" scale="1.4"></icon>
    </div>
    <div class="edit" @click="edit" v-show="editable">
      <icon class="icon-edit" name="ellipsis-h" scale="1.5"></icon>
    </div>
    <h1 class="title" v-html="title"></h1>
    <div class="bg-image" ref="bgImage" :style="bgStyle" :class="{ compress: scrollToTop, scaling }">
      <div class="play-wrapper" v-show="songs.length">
        <div class="play" ref="playBtn" @click="randomPlay(songs)">
          <icon class="icon-play" name="play-circle-o" scale="1.25"></icon>
          <span class="text">随机播放全部</span>
        </div>
      </div>
      <div class="filter" ref="filter"></div>
    </div>
    <div class="scroll-layer" ref="scrollLayer"></div>
    <scroller :onScroll="onScroll" :data="songs" class="list" ref="scroller">
      <div class="song-list-wrapper">
        <song-list
        :songs="songs"
        :rank="rank"
        :showLoading="!editable"
        @selectItem="selectSong"
        @edit="editSong"
        ></song-list>
      </div>
      <div class="no-result-wrapper" v-show="editable && songs.length < 1">
        <no-result title="还没有歌曲，快往歌单里添加歌曲吧~" code="300"></no-result>
      </div>
    </scroller>
  </div>
</template>

<script>
  import { mapActions } from 'vuex'
  import * as atypes from '@/store/action-types'
  import SongList from 'components/base/song-list/song-list'
  import Scroller from 'components/base/scroller/scroller'
  import NoResult from 'components/base/no-result/no-result'
  import PlayerMixin from '@/mixins/player-mixin'
  import { miniPlayerHeight } from 'assets/js/config'
  import { css } from 'assets/js/util'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/angle-left'
  import 'vue-awesome/icons/play-circle-o'
  import 'vue-awesome/icons/star'
  import 'vue-awesome/icons/star-o'
  import 'vue-awesome/icons/ellipsis-h'

  const defaultBg = require('assets/images/default_disc.jpg')
  const headerHeight = 40

  export default {
    name: 'music-list',
    components: { Icon, SongList, Scroller, NoResult },
    mixins: [PlayerMixin],
    props: {
      bgImage: {
        type: String,
        default: ''
      },
      songs: {
        type: Array,
        default: () => []
      },
      rank: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: ''
      },
      collectable: {
        type: Boolean,
        default: false
      },
      collected: {
        type: Boolean,
        default: false
      },
      editable: {
        type: Boolean,
        default: false
      }
    },
    created () {
      this.imageHeight = 0
    },
    mounted () {
      this.imageHeight = this.$refs.bgImage.clientHeight
      this.$refs.scroller.$el.style.top = `${this.imageHeight}px`
    },
    data () {
      return {
        scrollToTop: false,
        scaling: false,
        defaultBg
      }
    },
    computed: {
      bgStyle () {
        return {
          backgroundImage: `url(${this.bgImage || (this.editable ? this.defaultBg : '')})`
        }
      },
      collectIcon () {
        return this.collected ? 'star' : 'star-o'
      }
    },
    methods: {
      ...mapActions({
        addSong: atypes.ADD_SONG_AND_PLAY,
        randomPlay: atypes.RANDOM_PLAY,
        selectAndPlay: atypes.SELECT_AND_PLAY
      }),
      back () {
        this.$router.back()
      },
      toggleCollect () {
        this.$emit('toggleCollect')
      },
      edit () {
        this.$emit('edit')
      },
      onScroll ({ y }) {
        const maxScroll = this.imageHeight - headerHeight
        const percent = Math.abs(y / this.imageHeight)
        if (y <= 0) {
          this.scaling = false
          let scrollY = 0
          if (-y >= maxScroll) {
            scrollY = maxScroll
            this.scrollToTop = true
          } else {
            this.scrollToTop = false
            scrollY = y
          }
          const blur = Math.min(20 * percent, 20)
          css(this.$refs.filter, 'backdropFilter', `blur(${blur}px)`)
          css(this.$refs.scrollLayer, 'transform', `translate3d(0, ${scrollY}px, 0)`)
        } else if (y > 0) {
          const scale = 1 + percent
          this.scaling = true
          css(this.$refs.bgImage, 'transform', `translate3d(0, 0, 0) scale(${scale})`)
        }
      },
      selectSong (song, index) {
        this.selectAndPlay({
          index, list: this.songs
        })
      },
      editSong (event, song, index) {
        const { pageX, pageY, offsetX, offsetY, target } = event
        const actions = [
          {
            desc: '添加到播放列表',
            operation: () => {
              this.addSong(song)
            }
          },
          {
            desc: '添加到我的歌单',
            operation: () => {
              this.$emit('addSongToMyDisc', song)
            }
          }
        ]
        if (this.editable) {
          actions.push({
            desc: '移除',
            operation: () => {
              this.$emit('removeSongFromMyDisc', song)
            }
          })
        }
        this.$popup.show({
          x: pageX - offsetX + target.clientWidth / 2,
          y: pageY - offsetY + target.clientHeight / 2,
          actions
        })
      },
      handlePlaylist (playlist) {
        const bottom = playlist.length > 0 ? miniPlayerHeight : 0
        this.$refs.musiclist.style.bottom = bottom
        this.$refs.scroller.refresh()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .music-list {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: @color-background;

    .back {
      position: absolute;
      top: 0;
      left: 3px;
      z-index: 110;
      .icon-back {
        display: block;
        padding: 2px 10px 10px 10px;
        color: @color-theme;
      }
    }

    .collect, .edit {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 110;
      .icon-collect, .icon-edit {
        display: block;
        padding: 8px 8px 10px 10px;
        color: @color-theme;
      }
    }

    .title {
      position: absolute;
      top: 0;
      left: 10%;
      z-index: 110;
      width: 80%;
      .no-wrap();
      text-align: center;
      line-height: 40px;
      font-size: @font-size-large;
      color: @color-text;
    }

    .bg-image {
      position: relative;
      width: 100%;
      height: 0;
      padding-top: 70%;
      background: @color-background no-repeat center top;
      transform-origin: top;
      background-size: cover;
      &.compress {
        padding-top: 0;
        height: 40px;
        z-index: 106;
        .play-wrapper {
          display: none;
        }
      }
      &.scaling {
        z-index: 106;
      }
      .play-wrapper {
        position: absolute;
        bottom: 25px;
        z-index: 2;
        width: 100%;
        .play {
          box-sizing: border-box;
          line-height: 1;
          width: 135px;
          padding: 7px 0;
          margin: 0 auto;
          text-align: center;
          border: 1px solid @color-theme;
          color: @color-theme;
          border-radius: 100px;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 0;
          .icon-play {
            display: inline-block;
            margin-right: 6px;
          }
          .text {
            font-weight: bold;
            display: inline-block;
            font-size: @font-size-small;
          }
        }
      }
      .filter {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background: rgba(7, 17, 27, 0.25);
      }
    }

    .no-result-wrapper {
      text-align: center;
    }

    .scroll-layer {
      position: relative;
      height: 100%;
      background: @color-background;
      z-index: 104;
    }

    .list {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 100%;
      z-index: 105;
      background: @color-background;
      .song-list-wrapper {
        padding: 20px 30px;
      }
    }
  }
</style>



