<template lang="html">
  <transition name="slide">
    <music-list
    :title="title"
    :bg-image="bgImage"
    :songs="songs"
    :collectable="collectable"
    :collected="collected"
    :editable="editable"
    @edit="edit"
    @toggleCollect="toggleCollect"
    @addSongToMyDisc="handleAddSong"
    @removeSongFromMyDisc="handleRemoveSong"
    ></music-list>
  </transition>
</template>

<script>
import * as atypes from '@/store/action-types'
import { mapGetters, mapActions } from 'vuex'
import { getDiscSongList } from 'api/recommend'
import { ERR_OK } from 'api/config'
import { catchErrors } from 'api/handler'
import { timeout } from 'assets/js/util'
import Song from 'assets/js/song'

import MusicList from 'components/base/music-list/music-list'

export default {
  name: 'disc',
  components: { MusicList },
  async created () {
    await timeout(500)
    this._fetchData()
  },
  beforeRouteUpdate (to, from, next) {
    this._fetchData(to.params.dissid)
    next()
  },
  data () {
    return {
      disc: {},
      editable: false,
      collectable: true,
      collected: false
    }
  },
  computed: {
    ...mapGetters([
      'myDiscList', 'collectDiscList'
    ]),
    title () {
      return this.disc.name || ''
    },
    bgImage () {
      return this.disc.imgurl || ''
    },
    songs () {
      return this._formatSongs(this.disc.songs || [])
    },
    resetMyDiscList () {
      return this.myDiscList.filter(disc => disc.id !== this.disc.id)
    }
  },
  methods: {
    ...mapActions({
      cacheCollect: atypes.CACHE_COLLECT,
      decacheCollect: atypes.DECACHE_COLLECT,
      addSongToMyDisc: atypes.ADD_SONG_TO_MY_DISC,
      removeSongFromMyDisc: atypes.REMOVE_SONG_FROM_MY_DISC,
      clearMyDisc: atypes.CLEAR_MY_DISC,
      changeName: atypes.CHANGE_MY_DISC_NAME
    }),
    init () {
      this.disc = {}
      this.collectable = true
      this.collected = false
    },
    toggleCollect () {
      if (this.collected) {
        this.decacheCollect(this.disc)
        this.collected = false
      } else {
        this.cacheCollect(this.disc)
        this.collected = true
      }
    },
    edit () {
      const actions = [
        {
          desc: '清空',
          operation: () => {
            if (this.disc.songs.length > 0) {
              this.$dialog.confirm({
                title: '确定要清除此歌单？',
                confirm: () => {
                  this.clearMyDisc(this.disc)
                }
              })
            }
          }
        },
        {
          desc: '修改名称',
          operation: () => {
            this.$dialog.prompt({
              title: '修改名称',
              placeholder: '请输入歌单新名称',
              confirm: name => {
                this.changeName({ disc: this.disc, name })
              }
            })
          }
        }
      ]
      this.$actionsheet.show({ title: '操作', actions })
    },
    handleAddSong (song) {
      const actions = this.resetMyDiscList.map(disc => {
        return {
          desc: disc.name,
          operation: () => {
            this.addSongToMyDisc({ disc, song })
          }
        }
      })
      this.$actionsheet.show({ title: '请选择要加入的歌单', actions })
    },
    handleRemoveSong (song) {
      this.removeSongFromMyDisc({ disc: this.disc, song })
    },
    /**
     * 获取歌单信息
     */
    _fetchData (dissid = this.$route.params.dissid) {
      this.init()
      if (!dissid) {
        return this.$router.back()
      }
      if (this._getMyDisc(dissid)) {
        this.collectable = false
        this.editable = true
        return
      }
      if (this._getCollectDisc(dissid)) {
        this.collected = true
        return
      }
      this._getDiscSongList(dissid)
    },
    /**
     * 获取自建歌单
     * @param  {Number} dissid 歌单id
     * @return {Boolean} 是否在自建歌单中找到
     */
    _getMyDisc (dissid) {
      let disc = this.myDiscList.find(v => v.id === dissid)
      if (disc) {
        this.disc = disc
        return true
      }
      return false
    },
    /**
     * 获取收藏歌单
     * @param  {Number} dissid 歌单id
     * @return {Boolean} 是否在收藏歌单中找到
     */
    _getCollectDisc (dissid) {
      let disc = this.collectDiscList.find(v => v.id === dissid)
      if (disc) {
        this.disc = disc
        return true
      }
      return false
    },
    /**
     * 获取歌单的歌曲数据
     * @param  {Number} dissid 歌单id
     */
    _getDiscSongList: catchErrors(async function (dissid) {
      const discSongRes = await getDiscSongList(dissid)
      if (discSongRes && discSongRes.code === ERR_OK) {
        const { disstid: id, dissname: name, logo: imgurl, nickname: creator, songlist: songs } = discSongRes.cdlist[0]
        this.disc = { id, name, imgurl, creator, songs }
      } else {
        this.$router.back()
      }
    }),
    /**
     * [格式化歌曲数据]
     * @param {Array} songs 接口获取的歌曲数据
     */
    _formatSongs (songs) {
      const formattedSongs = []
      songs.forEach(song => {
        if (song.songid && song.albummid) {
          formattedSongs.push(Song.create(song))
        } else if (song.id && song.album) {
          formattedSongs.push(song)
        }
      })
      return formattedSongs
    }
  }
}
</script>

<style scoped lang="less">

  .slide-enter-active, .slide-leave-active {
    transition: all .3s;
  }
  .slide-enter, .slide-leave-to {
    transform: translate(100%, 0);
  }

</style>



